.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.main h1 {
  display: flex;
  flex-direction: column;
  font-size: 2em;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  text-align: center;
  width: 80%;
  margin: 10px auto;
  padding: 0.3em;
  text-transform: uppercase;
}

.content {
  width: 80%;
  margin: 10px auto;
}

.content p {
  padding: 1em;
  text-align: center;
}

@media screen and (min-width: 481px) {
  .content {
    margin: auto;
  }
  .content p {
    font-size: 1.5em;
  }
  .main h1 {
    margin: 30px auto -40px auto;
  }
}
